import React from 'react';
import { Route } from 'react-router-dom';

// Components
import ContactForm from './ContactForm';
import ThankYou from './ThankYou';

const ContactSection = (props) => {
  return (
    <section id="contact" className="section contact">
      <div className="wrapper">
        <h2 className="section__title contact">
          Contact
        </h2>
        <hr />
        <p>
          Une question ? Envie de travailler ensemble ?
        </p>
        <p>
          Vous pouvez m'envoyer un email à l'adresse <a href="http://www.google.com/recaptcha/mailhide/d?k=01k10yCxni6j0afa994fA5FQ==&amp;c=x-sLvXggfI6fyFoUlfXYDlvBRI_yFa1RfYtaIRdAyT0=" onClick={e => {e.preventDefault(); window.open('http://www.google.com/recaptcha/mailhide/d?k\x3d01k10yCxni6j0afa994fA5FQ\x3d\x3d\x26c\x3dx-sLvXggfI6fyFoUlfXYDlvBRI_yFa1RfYtaIRdAyT0\x3d', '', 'toolbar=0,scrollbars=0,location=0,statusbar=0,menubar=0,resizable=0,width=500,height=300'); return false;}} title="Reveal this e-mail address">t...@vandenhede.me</a> ou bien remplir le formulaire ci-dessous.
        </p>
        <hr />

        <Route exact path="/contact" component={ ContactForm } />
        <Route exact path="/thank-you" component={ ThankYou } />

      </div>
    </section>
  );
};

export default ContactSection;


