import React from 'react';

// Components

const ContactForm = (props) => {
  return (
    <form class="contact-form" action="//formspree.io/thomas@vandenhede.me" method="POST">
      <h3>Vous souhaitez m'écrire ? J'ai tout prévu ;).</h3>
      <p>
        Vous vous appelez <input className="contact-form__input" type="text" name="name" placeholder="Jean Dupont" required /> et vous souhaiteriez m'écrire de l'adresse <input className="contact-form__input" type="email" name="_replyto" placeholder="jeandupont@exemple.fr" required /> pour me dire :<br />
        <textarea className="contact-form__textarea" name="message" rows="2" placeholder="Message" required></textarea>
        <input className="hidden" type="text" name="_gotcha" style={{'display':'none'}} />
        <input className="hidden" type="hidden" name="_subject" value="Message via http://domain.com" />
        <input type="hidden" name="_next" value="/thank-you" />
      </p>
      <p>
        <input className="btn btn--rounded btn--shadow submit" type="submit" value="Envoyer" />
      </p>
    </form>
  );
};

export default ContactForm;


