import React from 'react';
import { Link } from 'react-router-dom';

// Components

const ThankYou = (props) => {
  return (
    <div>
      <p>
        Merci pour votre superbe message !<br />
        Cliquez sur le bouton ci-dessous pour m'écrire à nouveau. On n'abuse pas des bonnes choses.
      </p>
      <Link className="btn btn--rounded btn--shadow" to="/contact">nouveau message</Link>
    </div>
  );
};

export default ThankYou;


